import React from 'react';
import { NavBar } from 'antd-mobile';
import { Menu, Dropdown, Button } from 'antd';
import { DownOutlined, HomeOutlined } from '@ant-design/icons';

import { Link, navigate } from 'gatsby';

const DropdownMenu = () => {
  const menu = (
    <Menu>
      <Menu.Item key="0">
        <Link className="nav-link" to="/"></Link>
      </Menu.Item>
      <Menu.Item key="1">
        <Link className="nav-link" to="/logistic"></Link>
      </Menu.Item>
      <Menu.Item key="2">
        <Link className="nav-link" to="/storemanage"></Link>
      </Menu.Item>
      <Menu.Item key="3">
        <Link className="nav-link" to="/navstatistic"></Link>
      </Menu.Item>
      <Menu.Item key="4">
        <Link className="nav-link" to="/manage"></Link>
      </Menu.Item>
      <Menu.Divider />
      <Menu.Item key="5"><Link className="nav-link" to="/login"></Link></Menu.Item>
    </Menu>
  );

  return (
    <Dropdown overlay={menu} trigger={['click']}>
      <Button className="ant-dropdown-link">
        <div>Menu <DownOutlined /></div>
      </Button>
    </Dropdown>
  );
}

const Header = () => {
  return (
    <NavBar
      mode="light"
      icon={<HomeOutlined />}
      onLeftClick={() => navigate('/')}
      rightContent={[
        <DropdownMenu key="1"></DropdownMenu>,
      ]}
    ></NavBar>
  )
}

export default Header;
